import React from 'react';
import mod from "../scss/modules/ratingStar.module.scss"

type props = {
  rating: number
}
const RatingStar: React.FC<props> = (props) => {
  const number = props.rating
  const integer = Math.floor(number) // ratingã®æ•´æ•°éƒ¨åˆ†
  const decimal = (number - integer) // ratingã®å°æ•°éƒ¨åˆ†
  const adjustedDecimal = decimal === 0 ? 0 : 0.5 + (decimal - 0.5) * 0.5 // å°æ•°éƒ¨åˆ†ã‚’è¦‹ãˆã‚„ã™ãã™ã‚‹ãŸã‚ã«èª¿ç¯€
  return (
    <div className={mod.ratingStar01}>
      <span>
        <span>â˜…</span>
        <span className={integer >= 0 ? mod.active : undefined}>â˜…</span>
      </span>
      <span>
        <span>â˜…</span>
        <span className={integer >= 1 ? mod.active : undefined} style={integer === 1 ? { width: adjustedDecimal * 100 + "%" } : undefined}>â˜…</span>
      </span>
      <span>
        <span>â˜…</span>
        <span className={integer >= 2 ? mod.active : undefined} style={integer === 2 ? { width: adjustedDecimal * 100 + "%" } : undefined}>â˜…</span>
      </span>
      <span>
        <span>â˜…</span>
        <span className={integer >= 3 ? mod.active : undefined} style={integer === 3 ? { width: adjustedDecimal * 100 + "%" } : undefined}>â˜…</span>
      </span>
      <span>
        <span>â˜…</span>
        <span className={integer >= 4 ? mod.active : undefined} style={integer === 4 ? { width: adjustedDecimal * 100 + "%" } : undefined}>â˜…</span>
      </span>
      <p className={mod.num}>{number}</p>
    </div >
  )
}

export default RatingStar
